import countries from '../data/countries';
import { localeService } from '../../common/localeService';

/**
 * Given a valid locale it returns the correspondent prefix if found or +44 otherwise.
 * @param {string} locale - a string that represent the locale ex:'es-ES'
 * @returns {string}
 */
export const setDefaultPrefix = locale => {
  const localeCountry = localeService.getCountryFromLocale(locale);
  const country = findCountryByCode(localeCountry);
  let prefix = '+44';
  // let format = '';
  if (country) {
    prefix = country.phone;
    // Imported from angular but it seems is not used atm.
    // format = country.phoneFormat &&  country.phoneFormat ;
  }
  return prefix;
};

export const isValidPhoneNumber = phoneNumber =>
  typeof phoneNumber === 'string' && phoneNumber.length > 4 && phoneNumber.substring(0, 1) === '+';

export const longestMatchingPrefix = matchingCodes =>
  matchingCodes.reduce((a, b) => (a.phone.length > b.phone.length ? a : b));

export const findCountryByPrefix = number => {
  let matchingCodes = null;
  if (number && number.length > 1) {
    matchingCodes = countries.filter(country => number.indexOf(country.phone) === 0);
  }
  return matchingCodes && matchingCodes.length ? longestMatchingPrefix(matchingCodes) : null;
};

export const findCountryByCode = code => {
  let matchingCodes;
  if (code && code.length === 2) {
    matchingCodes = countries.filter(country => code === country.iso2);
  }
  return matchingCodes && matchingCodes.length ? longestMatchingPrefix(matchingCodes) : null;
};

/**
 * Given a sting in a valid format ex:'+447573135343' it returns an object of shape
 * {prefix=+44 ,suffix=7573135343}
 * @param {string} number - a string that defines a phone number.
 * @param {string} locale - a sting that defines the current locale ex:es-ES.
 * @returns {{prefix: (string|*), suffix: string, format: string}}
 */
export const explodeNumberModel = (number, locale) => {
  const country = findCountryByPrefix(number);
  let prefix = '';
  let suffix = '';
  let format = '';
  if (isValidPhoneNumber(number)) {
    if (country) {
      prefix = country.phone;
      suffix = number.substring(country.phone.length);
      format = country.phoneFormat || '';
    } else {
      prefix = '';
      suffix = number.substring(1);
      format = '';
    }
  } else {
    prefix = setDefaultPrefix(locale);
  }
  return { prefix, suffix, format };
};



