import React, { PureComponent } from 'react';
import Types from 'prop-types';

import Select from '../select';
import { isValidPhoneNumber, explodeNumberModel } from './utils/phoneNumberUtils';
import { filterOptionsForQuery } from './utils/filterUtils';
import countries from './data/countries';

class PhoneNumberInput extends PureComponent {
  static propTypes = {
    required: Types.bool,
    disabled: Types.bool,
    value: Types.string,
    onChange: Types.func.isRequired,
    locale: Types.string,
    searchPlaceholder: Types.string,
    size: Types.oneOf(['sm', 'md', 'lg']),
  };

  static defaultProps = {
    required: false,
    disabled: false,
    value: '',
    locale: 'en_GB',
    searchPlaceholder: 'Prefix',
    size: 'md',
  };

  /* eslint-disable react/sort-comp */
  explodedNumber = explodeNumberModel(this.props.value || '', this.props.locale);

  state = {
    suffix: (this.explodedNumber && this.explodedNumber.suffix) || '',
    prefix: { value: this.explodedNumber.prefix, label: this.explodedNumber.prefix },
    searchQuery: '',
  };

  static getDerivedStateFromProps(props, state) {
    if (isValidPhoneNumber(props.value)) {
      const { prefix, suffix } = explodeNumberModel(props.value, props.locale);
      if (prefix !== state.prefix.value && state.suffix !== suffix) {
        return {
          suffix,
          prefix: { value: prefix, label: prefix },
          searchQuery: '',
        };
      }
      return null;
    }
    return null;
  }

  onChangeSearch = searchQuery => {
    this.setState({ searchQuery });
  };

  getSelectOptions = () => {
    const options = [];
    const filteredOptions = this.state.searchQuery
      ? filterOptionsForQuery(countries, this.state.searchQuery)
      : countries;
    filteredOptions.forEach(option => {
      const { phone, iso3, iso2 } = option;
      options.push({ value: phone, label: phone, note: iso3 || iso2 });
    });
    return options;
  };

  handleChangeSelect = event => {
    this.setState(
      {
        searchQuery: '',
        prefix: { value: event.value, label: event.label },
      },
      () => {
        const { prefix, suffix } = this.state;
        this.props.onChange({ prefix: prefix.value, suffix });
      },
    );
  };

  handleInputChange = event => {
    this.setState(
      {
        suffix: event.target.value,
      },
      () => {
        const { prefix, suffix } = this.state;
        this.props.onChange({ prefix: prefix.value, suffix });
      },
    );
  };

  render() {
    const { searchPlaceholder, disabled, required, size } = this.props;
    const selectOptions = this.getSelectOptions();
    const { prefix, suffix } = this.state;
    return (
      <div className="tw-telephone">
        <div className="row">
          <div className="col-xs-5">
            <Select
              options={selectOptions}
              selected={prefix}
              onChange={this.handleChangeSelect}
              searchPlaceholder={searchPlaceholder}
              onSearchChange={this.onChangeSearch}
              searchValue={this.state.searchQuery}
              required={required}
              disabled={disabled}
              size={size}
            />
          </div>

          <div className="col-xs-7">
            <div className={`input-group input-group-${size}`}>
              <input
                name="phoneNumber"
                value={suffix}
                type="text"
                className="form-control"
                disabled={disabled}
                onChange={this.handleInputChange}
                required={required}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default PhoneNumberInput;



